import styled, { css } from 'styled-components'

import { Spacing } from '../styling'

interface IFormInputStyled {
  invalid?: boolean
  inputFullWidth?: boolean
}

export const invalidInputStyle = css`
  border: 1px red solid;
`

export const fullWidthStyle = css`
  width: 100%;
`

export const FormInputStyled = styled.input`
  margin: ${Spacing.MarginAroundFormComponents};
  ${({ inputFullWidth }: IFormInputStyled) => inputFullWidth && fullWidthStyle};
  ${({ invalid }: IFormInputStyled) => invalid && invalidInputStyle};
`
