import React from 'react'
import { Redirect } from 'react-router-dom'

import { useRoot } from '../../../atoms/RootProvider'
import { PasswordResetPage } from '../pages/PasswordResetPage'

export const PasswordResetRoute: React.FC = () => {
  const { auth } = useRoot()
  return auth.isAuthenticatedUser ? <Redirect to="/" /> : <PasswordResetPage />
}

export default PasswordResetRoute
